
.character {
  position: relative;
  display: block;
  left: 0px;
  top: 0px;
  bottom: 0;
  z-index: 1;
  height: 100%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.news {
  position: fixed;
  z-index: 10;
  right: 0;
  top: 0;
  padding: 25px;
  color: #FFF;
  margin: 20px;
}
.news .title {
  position: relative;
  padding: 0 6px;
  width: auto;
  font-size: 13px;
}
.news .title:after,
.news .title:before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: url(/static/line_white.png) center center no-repeat;
  background-size: 100% 1px;
  position: absolute;
  left: 0;
  opacity: 0.7;
  box-shadow: 0px 2px 1px 0 rgba(0, 0, 0, 0.75);
}
.news .title:after {
  bottom: -6px;
}
.news .title:before {
  top: -9px;
}
.news .list {
  margin-top: 18px;
  font-size: 16px;
  font-family: UniversCom59UltraCondensed;
}
.news .list .item {
  margin-bottom: 5px;
}
.news .list .item small {
  font-size: 12px;
  color: #ccc;
}
.news .list .item a {
  color: #fff;
  text-decoration: none;
  transition: all .2s;
}
.news .list .item a:hover {
  color: #ccc;
}



